    font-family: 'Kanit', sans-serif;
}

.home {
    position: fixed;
    margin: 0;
    height: 100%;
    width: 100%;

}

.main {
    width: 100%;
    height: 100%;
    margin: 0, 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

