    width: 100%;
}

h2 {
    color: white;
    font-size: 3rem;
    backdrop-filter: blur(8px);
    background: linear-gradient(rgba(0, 0, 0, 0.75), rgb(7 0 0 / 0));
    /* background-color: #000000; */
    /* -webkit-filter: blur(8px); */
    position: fixed;
    height: 7rem;
    padding-top: 2rem;
    z-index: 10;
    width: 100%;
    font-family: 'Kanit', sans-serif;
    text-shadow: 10px #000000;
    mask-image: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

