.start_button {
    position: fixed;
    border: none;
    top: 50%;
    left: 50%;
    height: 16rem;
    width: 16rem;
    margin-top: -8rem;
    margin-left: -8rem;
    font-size: 3rem;
    border-radius: 50%;
    color: white;
    box-shadow: 15px 15px 0 0 rgb(197, 112, 0);
    /* z-index: 15; */
    /* backdrop-filter: blur(5px); */
    background-color: rgb(252 153 24);
    /* animation: infinite; */
    /* animation-delay: 3s; */
    transition: 200ms;
}

.start_button:hover {
    transform: translate3d(0, 0, -500px);
    opacity: 0.8;
    cursor: grab;

}

.start_button:active {
    transform: translate(0, 10px);
    transform: scale(1.1, 0.9);
    /* transform: scale(0.9, 1.1); */
    opacity: 0;
    box-shadow: 5px 5px 0 0 rgb(197, 112, 0);
    cursor: grabbing;
}


.home_button {
    border-radius: 0 0 100% 0;
    text-align: left;
    padding-left: 2rem;
    font-size: 4rem;
}

.clear_button {
    border-radius: 0 0 0 100%;
    text-align: right;
    padding-right: 2rem;
    font-size: 3rem;
}

.clear_button,
.home_button {
    border: none;
    width: 8rem;
    height: 100%;
    text-decoration: none;
    padding-bottom: 1.5rem;
    /* font-size: 2rem; */
    color: #370665;
    background-color: #F14A16;

}

.clear_button:hover,
.home_button:hover {
    transform: scale(1.5);
    /* border: 1px solid rgb(255, 103, 57); */
    box-shadow: 0 0 10px 5px #ff764c;
    color: white;
    transition: 100ms;
    animation-duration: 100ms;
}


.exeunt {
    animation-duration: 1s;
    animation-name: removeStart;
}

@keyframes removeStart {
    from {
        transform: translate(20px, 40px);
    }

    to {
        transform: translate(-40px, 1000px);
    }
}